.nutrition {
  width: 100%;

  &__title {
    margin: 0;
    margin-bottom: 37px;
    padding: 0;
    font-family: "Oswald", sans-serif;
    font-weight: normal;
    font-size: 36px;
    line-height: 36px;
    color: @basic_black;

    @media (min-width: @tablet-width) {
      margin-bottom: 55px;
      font-size: 60px;
      line-height: 60px;
    }
  }

  &__wrapper {
    margin: 0 auto;
    padding: 27px 20px 30px;
    width: 280px;

    @media (min-width: @tablet-width) {
      padding: 65px 40px 60px;
      width: 708px;
    }

    @media (min-width: @desktop-width) {
      padding: 74px 110px 79px;
      width: 1220px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;

  &__description {
    margin: 0;
    margin-bottom: 39px;
    padding: 0;
    font-family: "Oswald", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: @basic_black;
    text-transform: uppercase;
    max-width: 226px;

    @media (min-width: @tablet-width) {
      margin-bottom: 66px;
      font-size: 20px;
      line-height: 30px;
      max-width: 323px;
    }
  }

  &__conteiner {
    display: flex;
    flex-direction: column;

    @media (min-width: @tablet-width) {
      margin-bottom: 65px;
    }

    @media (min-width: @desktop-width) {
      display: grid;
      grid-template-columns: repeat(2, 570px);
      gap: 80px;
      margin-bottom: 70px;
    }
  }

  &__client {
    display: flex;
    flex-direction: column;
    margin: 0 -20px 25px;
    padding: 0 20px 21px;
    border-bottom: 1px solid @special_grey-3;

    @media (min-width: @tablet-width) {
      border-bottom: none;
      margin: 0;
      margin-bottom: 72px;
      padding: 0;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 0;
    }
  }

  &__text {
    margin-bottom: 14px;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @media (min-width: @tablet-width) {
      margin-bottom: 20px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 0;
    }
  }

  &__client &__text {
    @media (min-width: @desktop-width) {
      margin-bottom: 20px;
    }
  }

  &__text:last-child {
    margin-bottom: 0;
  }

  &__label {
    margin-right: 13px;
    font-family: "Oswald", sans-serif;
    font-size: 16px;
    line-height: 18px;
    text-wrap: nowrap;

    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }
    color: @special_dark-grey;
    text-transform: uppercase;
  }

  &__field-wrapper {
    position: relative;
  }

  &__field-image {
    stroke: @basic_green;
    position: absolute;
    right: -20px;
    top: 15px;
  }

  &__field {
    width: 100%;
    font-family: "Oswald", sans-serif;
    font-size: 16px;
    line-height: 18px;
    padding: 14px 15px 13px;
    border: 1px solid @basic_grey-5;

    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
      padding: 11px 20px;
    }

    @media (min-width: @desktop-width) {
      justify-self: stretch;
    }

    color: @special_dark-grey;
    text-transform: uppercase;
  }

  &__field:invalid {
    border: 1px solid @status_error;
  }

  &__field:invalid + &__field-image path {
    stroke: @status_error;
  }

  &__field:hover {
    border: 1px solid @special_grey;
  }

  &__field:focus {
    border: 1px solid @special_dark-grey;
  }

  &__field:focus + &__field-image path {
    stroke: @basic_green;
  }

  &__field:active {
    border: 1px solid @basic_grey-5;
  }

  &__client &__field {
    @media (min-width: @desktop-width) {
      justify-self: stretch;
    }
  }

  &__solution {
    margin: 0 -20px 29px;
    padding: 0 20px 16px;
    border: none;
    border-bottom: 1px solid @special_grey-3;

    @media (min-width: @tablet-width) {
      margin: 0;
      padding: 23px 27px 27px;
      border-bottom: none;
      border: 1px solid @basic_grey-5;
    }

    @media (min-width: @desktop-width) {
      padding: 25px 28px;
    }
  }

  &__solution-list {
    @media (min-width: @desktop-width) {
      display: flex;
      flex-direction: column;
    }
  }

  &__solution-item {
    margin-bottom: 13px;
    padding: 6px 0;

    @media (min-width: @tablet-width) {
      padding: 5px 0;
      margin-bottom: 32px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 13px;
    }
  }

  &__select {
    padding-left: 60px;
    position: relative;
  }

  &__input[type="radio"] + &__mark {
    position: absolute;
    top: -5px;
    left: 0;
    width: 30px;
    height: 30px;
    border: 1px solid @special_grey;
    border-radius: 50%;
    box-sizing: border-box;

    @media (min-width: @tablet-width) {
      width: 40px;
      height: 40px;
    }

    @media (min-width: @desktop-width) {
      top: 0;
    }
  }

  &__input[type="radio"]:hover + &__mark {
    border: 1px solid @special_grey-5;
  }

  &__input[type="radio"]:disabled + &__mark {
    opacity: 0.3;
  }

  &__input[type="radio"]:checked + &__mark {
    background-image: url("../../img/form-radio-checked.svg");
    background-size: 30px 30px;
    background-repeat: no-repeat;
    border: none;

    @media (min-width: @tablet-width) {
      background-size: 40px 40px;
    }
  }

  &__input[type="radio"]:checked:hover + &__mark {
    background-image: url("../../img/form-radio-checked-hover.svg");
  }

  &__input[type="radio"]:checked:disabled + &__mark {
    opacity: 0.3;
  }

  &__input[type="checkbox"] + &__mark {
    position: absolute;
    top: -5px;
    left: 0;
    width: 30px;
    height: 30px;
    border: 1px solid @special_grey;
    box-sizing: border-box;

    @media (min-width: @tablet-width) {
      width: 40px;
      height: 40px;
    }
  }

  &__input[type="checkbox"]:hover + &__mark {
    border: 1px solid @special_grey-5;
  }

  &__input[type="checkbox"]:disabled + &__mark {
    opacity: 0.3;
  }

  &__input[type="checkbox"]:checked + &__mark {
    width: 30px;
    height: 30px;
    border: 1px solid @special_grey;
    background-image: url("../../img/tick.svg");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-width: @tablet-width) {
      width: 40px;
      height: 40px;
    }
  }

  &__input[type="checkbox"]:checked:hover + &__mark {
    border: 1px solid @special_grey-5;
  }

  &__input[type="checkbox"]:checked:disabled + &__mark {
    opacity: 0.3;
  }

  &__owner {
    margin: 0;
    padding: 0;
    margin-bottom: 24px;

    @media (min-width: @tablet-width) {
      margin-bottom: 65px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 70px;
    }
  }

  &__owner &__field--tel::before {
    position: absolute;
    content: "";
    width: 20px;
    height: 20px;
    background-image: url("../../img/phone.svg");
    background-repeat: no-repeat;
    top: 0;
    right: 0;
  }

  &__title {
    display: inline-block;
    position: relative;
    font-family: "Oswald", sans-serif;
    font-weight: normal;
    font-size: 24px;
    line-height: 26px;
    color: @basic_black;

    @media (min-width: @tablet-width) {
      font-size: 36px;
      line-height: 36px;
    }
  }

  &__owner-title {
    margin: 0 -20px;
    margin-bottom: 35px;
    padding: 0;
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: @tablet-width) {
      margin: 0 -30px;
      margin-bottom: 76px;
      padding-left: 30px;
      padding-right: 30px;
    }

    @media (min-width: @desktop-width) {
      margin: 0 -110px;
      margin-bottom: 76px;
      padding-left: 110px;
      padding-right: 110px;
    }
  }

  &__owner-title::before {
    position: absolute;
    content: "";
    width: 1000%;
    height: 2px;
    background-color: @basic_green;
    top: 14px;
    left: 217px;

    @media (min-width: @tablet-width) {
      top: 20px;
      left: 592px;
    }

    @media (min-width: @desktop-width) {
      left: 652px;
    }
  }

  &__owner-title::after {
    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: @basic_green;
      top: 20px;
      left: -572px;
    }

    @media (min-width: @desktop-width) {
      left: -662px;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;

    @media (min-width: @desktop-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 80px;
    }
  }

  &__comment {
    margin-bottom: 23px;

    @media (min-width: @tablet-width) {
      margin-bottom: 65px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 70px;
    }
  }

  &__comment-title {
    margin: 0 -20px;
    padding: 0;
    margin-bottom: 38px;
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: @tablet-width) {
      margin: 0 -30px;
      margin-bottom: 87px;
      padding-left: 30px;
      padding-right: 30px;
    }

    @media (min-width: @desktop-width) {
      margin: 0 -110px;
      margin-bottom: 78px;
      padding-left: 110px;
      padding-right: 110px;
    }
  }

  &__comment-title::before {
    position: absolute;
    content: "";
    width: 1000%;
    height: 2px;
    background-color: @basic_green;
    top: 14px;
    left: 161px;

    @media (min-width: @tablet-width) {
      top: 20px;
      left: 242px;
    }

    @media (min-width: @desktop-width) {
      left: 344px;
    }
  }

  &__comment-title::after {
    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: @basic_green;
      top: 20px;
      left: -242px;
    }

    @media (min-width: @desktop-width) {
      left: -332px;
    }
  }

  &__comment-text {
    margin: 0;
  }

  &__comment-text &__label {
    margin: 0;
  }

  &__comment-content {
    max-width: 280px;
    min-height: 150px;
    box-sizing: border-box;
    padding-right: 14px;
    opacity: 0.85;

    @media (min-width: @tablet-width) {
      max-width: 100%;
      min-height: 175px;
      padding: 11px 20px;
    }
  }

  &__additional {
    margin: 0;
    margin-bottom: 34px;
    padding: 0;
    border: none;

    @media (min-width: @tablet-width) {
      margin-bottom: 52px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 60px;
    }
  }

  &__additional-title {
    margin: 0 -20px;
    padding: 0;
    margin-bottom: 34px;
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: @tablet-width) {
      margin: 0 -30px;
      margin-bottom: 57px;
      padding-left: 30px;
      padding-right: 30px;
    }

    @media (min-width: @desktop-width) {
      margin: 0 -110px;
      margin-bottom: 78px;
      padding-left: 110px;
      padding-right: 110px;
    }
  }

  &__additional-title::before {
    position: absolute;
    content: "";
    width: 1000%;
    height: 2px;
    background-color: @basic_green;
    top: 14px;
    left: 181px;

    @media (min-width: @tablet-width) {
      top: 20px;
      left: 282px;
    }

    @media (min-width: @desktop-width) {
      left: 384px;
    }
  }

  &__additional-title::after {
    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: @basic_green;
      top: 20px;
      left: -282px;
    }

    @media (min-width: @desktop-width) {
      left: -372px;
    }
  }

  &-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__additional-list {
    @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 221px);
      column-gap: 123px;
    }

    @media (min-width: @desktop-width) {
      display: flex;
    }
  }

  &__additional-item {
    margin-bottom: 13px;
    padding: 6px 0;

    @media (min-width: @tablet-width) {
      padding: 5px 0;
      margin-bottom: 32px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 0;
    }
  }

  &-item {
    font-family: "Oswald", sans-serif;
    font-size: 16px;
    line-height: 18px;
    color: @special_dark-grey;
    text-transform: uppercase;

    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }
  }

  &-item:last-child {
    margin-bottom: 0;
  }

  &__button-wrapper {
    display: flex;
    flex-direction: column;

    @media (min-width: @tablet-width) {
      flex-direction: row;
      justify-content: space-between;
      max-width: 518px;
    }
  }

  &__button {
    margin-bottom: 23px;

    @media (min-width: @tablet-width) {
      min-width: 284px;
      margin-bottom: 0;
    }
  }

  &__legend {
    align-self: center;
    font-family: "Lato", sans-serif;
    font-size: 14px;
    line-height: 16px;

    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 16px;
    }
    color: @basic_black;
  }
}
